/* General Reset */
header, nav, ul {
margin: 0;
padding: 0;
box-sizing: border-box;
}

/* Header Container */
header {
background-color: #003366;
color: white;
padding: 12px 24px;
box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
}

/* Flex Layout for Header Content */
.header-content {
display: flex;
justify-content: space-between;
align-items: center;
flex-wrap: wrap;
gap: 15px;
}

/* Logo Styling */
#navbar-logo img {
height: 48px;
width: auto;
display: block;
padding-right: 20px;
}

/* Navigation Layout */
nav {
display: flex;
flex-grow: 1;
justify-content: left;
align-items: center;
}

nav ul {
display: flex;
gap: 24px;
list-style: none;
margin: 0;
padding: 0;
align-items: center;
flex-wrap: wrap;
}

/* Nav Links */
nav ul a {
font-family: "Bebas Neue", serif;
text-decoration: none;
color: white;
font-size: 1.5rem;
padding: 8px 18px;
border-radius: 6px;
transition: background-color 0.3s ease, transform 0.2s ease, text-decoration 0.2s ease;
}

nav ul a:hover {
background-color: #00509e;
text-decoration: underline;
}

.menuBtn{
    width: 40px;
    height: 40px;
    display: none;
}

/* Info / Social Section */
.info {
display: flex;
flex-direction: column;
align-items: flex-end;
gap: 5px;
}

.info a {
font-family: "Bebas Neue", serif;
color: #ffffff;
font-size: 1.4rem;
text-decoration: none;
transition: color 0.3s ease;
}

.info a:hover {
color: #61b9ef;
}

@media (max-width: 768px) {
    .header-content {
        
        align-items: center;
        text-align: center;
        gap: 10px;
    }
  
    nav ul {
      flex-direction: row;        /* ‚Üê keep horizontal */
      flex-wrap: wrap;            /* ‚Üê allow wrapping */
      justify-content: center;
      gap: 10px;
    }
  
    nav ul a {
      font-size: 1.2rem;
      padding: 10px 16px;
    }
  
    .info {
        align-items: center;
    }
    .menuItems {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 70px; /* adjust based on your header height */
        right: 20px;
        background-color: #003366;
        width: 200px;
        border-radius: 5px;
        box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
        z-index: 1000;
      }
    
      .menuItems.open {
        display: flex;
      }
    
      .menuItems li {
        padding: 10px;
        text-align: left;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
      }
    
      .menuItems li:last-child {
        border-bottom: none;
      }
    
      .menuBtn {
        display: block;
        height: 30px;
        cursor: pointer;
      }
}
